<template>
    <div class="car-desc">
        <section class="container">
            <div class="row">
                <div class="col-xs-12">
                    <v-breadnav>{{`${secondHandCarDesc.carName} ${secondHandCarDesc.carSeriesName}`}}</v-breadnav>
                </div>
            </div>       
            <div class="row">
                <div class="col-xs-12">
                    <section class="desc-header clearfix">
                        <div class="col-xs-5">
                            <v-slider :slideArr = "getSliderArr"></v-slider>
                        </div>
                        <div class="col-xs-7">
                            <h3>{{`${secondHandCarDesc.carName} ${secondHandCarDesc.carSeriesName}`}}</h3>
                            <p class="price-refer">{{`${secondHandCarDesc.year} | ${secondHandCarDesc.kilomiter} | ${secondHandCarDesc.city}`}}</p>
                            <p class="price-refer">新车含税价：<del>{{`${secondHandCarDesc.containTaxPrice}`}}</del></p>
                            <h4 class="title-info">先用一年</h4>
                            <ul class="price-desc clearfix">
                                <li class="first-pay">                                   
                                    <p><del>{{`首付${secondHandCarDesc.firstPayment}`}}</del></p>
                                    <p><span class="price-num">0元</span></p>
                                </li>
                                <li class="month-pay">                                  
                                    <p>月供</p>
                                    <p><span class="price-num">{{secondHandCarDesc.monthPayment}}</span></p>
                                </li>
                                <li class="time-period">                                    
                                    <p>期数</p>
                                    <p><span class="price-num">{{`${secondHandCarDesc.periods}期`}}</span></p>
                                </li>
                            </ul>
                            <div class="next-year">
                                <div class="labels" v-if="Object.keys(secondHandCarDesc).length != 0 ">
                                    <span class="label-item" v-for="(item,index) in labels(secondHandCarDesc.labels)" :key="index">{{item}}</span>
                                </div>
                                <div class="after-oneyear">
                                     <!-- <h4 class="title-info">一年以后</h4>
                                     <p>{{`尾款购车${secondHandCarDesc.finalPayment}`}}</p>
                                     <p>还可以选择续租或退还车辆</p> -->
                                     <button class="btn pull-right">预约到店</button>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
            <div class="row">
                <div class="col-xs-12">
                    <section class="car-detail " v-if=" secondHandCarDesc" data-target="#fixed-navbar" data-offset="0">
                        <nav>
                            <a href="#car-details" class="car-desc active">车辆信息</a>
                            <a href="#base-config" class="base-config">基本配置</a>
                            <a href="#need-know" class="need-know">购车须知</a>
                            <a href="#buy-explain" class="buy-explain">购车说明</a>
                        </nav>
                        <article>
                             <div class="title" id="car-details">
                                <h3>车辆信息</h3>
                            </div>
                            <div class="car-detail-iframe" v-html="secondHandCarDesc.descUrl">
                            </div>
                            <div class="title" id="base-config">
                                <h3>基本配置</h3>
                                <a class="more pull-right" data-toggle="modal" data-target="#config-modal">查看全部</a>
                            </div>
                            <ul class="configs clearfix">
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车身机构</label>
                                        <span>{{ secondHandCarDesc.cheshenjiegou}}</span>
                                    </div>                                
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>长/宽/高</label>
                                        <span>{{`${ secondHandCarDesc.length}/${ secondHandCarDesc.width}/${ secondHandCarDesc.height}mm`}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>发动机</label>
                                        <span>{{ secondHandCarDesc.fadongjixinghao}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>变速箱</label>
                                        <span>{{ secondHandCarDesc.biansuxiangtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>驱动方式</label>
                                        <span>{{ secondHandCarDesc.qudongtype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>燃料形式</label>
                                        <span>{{ secondHandCarDesc.ranliaotype}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>综合油耗</label>
                                        <span>{{ secondHandCarDesc.cheshenjiegou}}</span>
                                    </div>
                                </li>
                                <li class="col-xs-6">
                                    <div class="config-item">
                                        <label>车辆规格</label>
                                        <span>{{ secondHandCarDesc.classType}}</span>
                                    </div>
                                </li>
                            </ul>
                            <div class="title" id="need-know">
                                <h3>购车须知</h3>
                            </div>
                            <div class="question-img">
                                <img :src="secondHandCarDesc.buyKnowUrl" alt="" class="img-responsive">
                            </div>
                             <div class="title" id="buy-explain">
                                <h3>购车说明</h3>
                            </div>
                            <div class="question-img">
                                <img :src="secondHandCarDesc.buyMarkUrl" alt="" class="img-responsive">
                            </div>                           
                        </article>
                    </section>
                </div>
            </div>
        </section>
        <section class="fixed-nav" v-show="scrollHeight>=620">
            <nav class="navbar navbar-default navbar-fixed-top">
                <div class="container">
                    <ul class="nav navbar-nav" id="fixed-navbar">
                        <li><a href="#car-details" class="active" >车辆信息</a></li>
                        <li><a href="#base-config" >基础配置</a></li>
                        <li> <a href="#need-know" class="need-know">购车须知</a></li>
                        <li> <a href="#buy-explain" class="buy-explain">购车说明</a></li>
                    </ul>
                    <button class="btn btn-default pull-right">预约到店</button>
                </div>
            </nav>
        </section>
        <section class="config-modal">
            <v-config :config ="configArr"></v-config>
        </section>
    </div>       
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import vSlider from "@/components/pc/slider"
import vConfig from "@/components/pc/carConfigModal"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
            carId:this.$route.params.id,
            secondHandCarDesc:{},
            scrollHeight:0,
            configArr:{}
        }
    },
    components:{
        vBreadnav,
        vSlider,
        vConfig 
    },
    methods:{
        getDescData:function(){
            let data = {};
            let _this = this;
            data.cartId = this.carId;
            data.platForm = "020003";
            $.ajax({
                url:PC_COMMONURL+'/querySecondhandCarDetail',
                type:"get",
                data:data,
                dataType:"json",
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.secondHandCarDesc = data.data.carDetail;
                        _this.configArr = Object.assign(data.data.params1,data.data.params2);
                    }
                }
            })
        },
        labels:function(labels){
            if(labels.indexOf(',')!= -1){
                console.log(labels.split(","));
                 return labels.split(",");
            }else{
                return [labels]
            }
           
        }
    },
    computed:{
        getSliderArr:function(){          
            let newSliderArr = [];
            if(Object.keys(this.secondHandCarDesc).length !=0 ){
                let slideArr = this.secondHandCarDesc.icons;
                slideArr.forEach(function(n){
                    let obj = new Object();
                    obj.imgUrl = n;
                    newSliderArr.push(obj);
                })               
            }
            return newSliderArr;
        }
    },
    mounted:function(){
        this.getDescData();
         let _this = this;
        $(document).scroll(function(){
            let scrollHeight = $(document).scrollTop();
            _this.scrollHeight = scrollHeight;
            if($("#base-config").offset() && $("#need-know").offset() && $("#buy-explain").offset() && $("#car-details").offset()){
                let baseHeight = $("#base-config").offset().top;
                let needKnowHeight =$("#need-know").offset().top;
                let buyExplaninHeight = $("#buy-explain").offset().top;
                let carDetailsHeight = $("#car-details").offset().top;
                if(carDetailsHeight-50 <= scrollHeight && scrollHeight < baseHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(0).find('a').addClass("active");
                }
                else if(baseHeight-50 <= scrollHeight && scrollHeight < needKnowHeight ){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(1).find('a').addClass("active");
                }else if(needKnowHeight-50  <= scrollHeight && scrollHeight < buyExplaninHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(2).find('a').addClass("active");
                }else if(buyExplaninHeight-50 <= scrollHeight){
                    $("#fixed-navbar a").removeClass("active");
                    $("#fixed-navbar li").eq(3).find('a').addClass("active");
                }
            }
            
        })  
    }
    
}
</script>



